@media screen and (max-width:1400px) {
    #profile{
        height: 83vh;
        margin-bottom: 6rem;
    }
    .about-containers{
        flex-wrap: wrap;
    }

}

@media screen and (max-width:1200px) {
    #desktop-nav{
        display: none;
    }
    #hamburger-nav{
        display: flex;
    }

    #experience, 
    .experience-details-container{
        margin-top: 2rem;
    }

    #experience{
        height: 150vh;
    }

    #about{
        height: 300vh;
        font-size: 25px;
        text-align: justify;
    }

    #project{
        height: 300vh;
    }
    #profile, 
    .section-container{
        display: block;
    }
    section{
        margin: 0 5%;
    }

    .section_pic-container{
        width: 275px;
        height: 275px;
        margin: 0 auto 2rem;
    }

    .about-containers{
        margin-top: 0;
    }

    .arrow{
        display: none;
    }
}

@media screen and (max-width:600px){
    #contact{
        margin-top: 2200px;
        height: 80vh;
    }
    #about{
        margin-top: 100px;
        height: 270vh;
    }
    footer{
        margin-top: 30px;
        height: 80vh;
    }
    #profile{
        height: 83vh;
        margin-bottom: 0;
    }
    #project{
        margin-top: 300px;
        height: 40vh;
    }
    article{
        font-size: 1rem;
    }
    footer nav{
        height: fit-content;
        
        margin-bottom: 2rem;
    }
    .about-containers,
    .contact-info-upper-container,
    .btn-container{
        flex-wrap: wrap;
    }
    .contact-info-container{
        margin: 0;
        flex-wrap: wrap;
    }
    

    .contact-info-container p,
    .nav-link li a{
        font-size: 1rem;
    }
    .experience-sub-title{
        font-size: 1.25rem;
    }
    .logo{
        font-size: 1.5rem;
    }
    .nav-link{
        flex-direction: column;
        gap: 0.5rem;
        text-align: center;
    }
    .section_pic-container{
        width: auto;
        height: 46vw;
        justify-content: center;
    }
    .section__text_p2{
        font-size: 1.25rem;
    }
    .title{
        font-size: 2rem;
    }
    .text-container{
        text-align: justify;
    }

    .arrow{
        display: none;
    }
    
}